import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

function PerformanceTest() {
  const [isRunning, setIsRunning] = useState(false);
  const [testResults, setTestResults] = useState<string[]>([]);

  const addResult = (result: string) => {
    setTestResults(prev => [...prev.slice(-4), result]); // 5
  };

  //  - 
  const runLongTask = (duration: number = 100) => {
    setIsRunning(true);
    addResult(`  ${duration}ms ...`);

    const start = performance.now();

    //  setTimeout 
    setTimeout(() => {
      while (performance.now() - start < duration) {
        // 
        Math.random();
      }

      const actualDuration = performance.now() - start;
      addResult(` :  ${actualDuration.toFixed(2)}ms`);
      setIsRunning(false);

      // 
      (window as any).monitor?.trackMetric('long_task_duration', actualDuration, {
        type: 'manual_test',
        expected: duration
      });
    }, 10);
  };

  // CPU 
  const runCPUIntensiveTask = () => {
    setIsRunning(true);
    addResult('  CPU ...');

    setTimeout(() => {
      const start = performance.now();

      // CPU 
      function fibonacci(n: number): number {
        if (n <= 1) return n;
        return fibonacci(n - 1) + fibonacci(n - 2);
      }

      const result = fibonacci(35); // 
      const duration = performance.now() - start;

      addResult(` : fib(35)=${result},  ${duration.toFixed(2)}ms`);
      setIsRunning(false);

      (window as any).monitor?.trackMetric('cpu_intensive_task', duration, {
        type: 'fibonacci',
        input: 35,
        result: result
      });
    }, 10);
  };

  // 
  const runMemoryTest = () => {
    setIsRunning(true);
    addResult(' ...');

    setTimeout(() => {
      const start = performance.now();

      // 
      const largeArray: number[][] = [];
      for (let i = 0; i < 1000; i++) {
        largeArray.push(new Array(1000).fill(Math.random()));
      }

      const duration = performance.now() - start;
      const memoryUsed = (largeArray.length * 1000 * 8) / (1024 * 1024); // MB

      addResult(` :  ${memoryUsed.toFixed(2)}MB,  ${duration.toFixed(2)}ms`);

      // 
      largeArray.length = 0;

      setIsRunning(false);

      (window as any).monitor?.trackMetric('memory_allocation', memoryUsed, {
        type: 'array_allocation',
        duration: duration
      });
    }, 10);
  };

  // DOM 
  const runDOMTest = () => {
    setIsRunning(true);
    addResult('  DOM ...');

    setTimeout(() => {
      const start = performance.now();
      const container = document.createElement('div');
      document.body.appendChild(container);

      //  DOM 
      for (let i = 0; i < 1000; i++) {
        const element = document.createElement('div');
        element.textContent = ` ${i}`;
        element.style.position = 'absolute';
        element.style.left = `${Math.random() * 100}px`;
        element.style.top = `${Math.random() * 100}px`;
        container.appendChild(element);
      }

      const duration = performance.now() - start;
      addResult(` DOM : 1000,  ${duration.toFixed(2)}ms`);

      //  DOM
      document.body.removeChild(container);
      setIsRunning(false);

      (window as any).monitor?.trackMetric('dom_operation', duration, {
        type: 'element_creation',
        count: 1000
      });
    }, 10);
  };

  // 
  const runNetworkTest = async () => {
    setIsRunning(true);
    addResult(' ...');

    const requests = [
      'http://localhost:3001/api/test/success',
      'http://localhost:3001/api/debug/stats',
    ];

    const results: number[] = [];

    for (const url of requests) {
      try {
        const start = performance.now();
        const response = await fetch(url);
        const duration = performance.now() - start;

        results.push(duration);
        await response.json(); // 

        addResult(` : ${duration.toFixed(2)}ms - ${response.status}`);
      } catch (error) {
        addResult(` : ${url}`);
      }
    }

    const avgDuration = results.reduce((a, b) => a + b, 0) / results.length;
    addResult(` :  ${avgDuration.toFixed(2)}ms`);

    (window as any).monitor?.trackMetric('network_performance', avgDuration, {
      type: 'api_requests',
      count: results.length
    });

    setIsRunning(false);
  };

  //  - API
  const collectPageMetrics = () => {
    if ('performance' in window) {
      // API
      const navigation = performance.getEntriesByType('navigation')[0] as PerformanceNavigationTiming;
      const paint = performance.getEntriesByType('paint');

      if (navigation) {
        const metrics = {
          'DNS': navigation.domainLookupEnd - navigation.domainLookupStart,
          'TCP': navigation.connectEnd - navigation.connectStart,
          '': navigation.loadEventEnd - navigation.fetchStart, //  fetchStart  navigationStart
          'DOM': navigation.domContentLoadedEventEnd - navigation.fetchStart, //  fetchStart
        };

        addResult(' :');
        Object.entries(metrics).forEach(([name, value]) => {
          addResult(`  ${name}: ${value.toFixed(2)}ms`);
          (window as any).monitor?.trackMetric(`page_${name}`, value, { type: 'navigation' });
        });
      } else {
        //  performance.timing API
        const timing = performance.timing;
        if (timing) {
          const metrics = {
            'DNS': timing.domainLookupEnd - timing.domainLookupStart,
            'TCP': timing.connectEnd - timing.connectStart,
            '': timing.loadEventEnd - timing.navigationStart,
            'DOM': timing.domContentLoadedEventEnd - timing.navigationStart,
          };

          addResult('  ():');
          Object.entries(metrics).forEach(([name, value]) => {
            addResult(`  ${name}: ${value.toFixed(2)}ms`);
            (window as any).monitor?.trackMetric(`page_${name}`, value, { type: 'navigation_legacy' });
          });
        }
      }

      paint.forEach(entry => {
        addResult(` ${entry.name}: ${entry.startTime.toFixed(2)}ms`);
        (window as any).monitor?.trackMetric(entry.name.replace('-', '_'), entry.startTime, { type: 'paint' });
      });
    }
  };

  // 
  useEffect(() => {
    collectPageMetrics();
  }, []);

  return (
    <div className="test-section">
      <h2> </h2>

      <div style={{ marginBottom: '20px' }}>
        <Link to="/">
          <button className="test-button"> </button>
        </Link>
      </div>

      {isRunning && (
        <div className="test-status info">
           ...
        </div>
      )}

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <button
          className="test-button"
          onClick={() => runLongTask(50)}
          disabled={isRunning}
        >
           50ms 
        </button>
        <button
          className="test-button"
          onClick={() => runLongTask(100)}
          disabled={isRunning}
        >
           100ms 
        </button>
        <button
          className="test-button danger"
          onClick={() => runLongTask(200)}
          disabled={isRunning}
        >
           200ms 
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <button
          className="test-button"
          onClick={runCPUIntensiveTask}
          disabled={isRunning}
        >
          CPU 
        </button>
        <button
          className="test-button"
          onClick={runMemoryTest}
          disabled={isRunning}
        >
          
        </button>
        <button
          className="test-button"
          onClick={runDOMTest}
          disabled={isRunning}
        >
          DOM 
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <button
          className="test-button"
          onClick={runNetworkTest}
          disabled={isRunning}
        >
          
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <button
          className="test-button"
          onClick={collectPageMetrics}
        >
          
        </button>
      </div>

      {testResults.length > 0 && (
        <div style={{ marginBottom: '20px' }}>
          <h3> </h3>
          <div style={{
            background: '#f5f5f5',
            padding: '12px',
            borderRadius: '4px',
            fontSize: '12px',
            fontFamily: 'monospace',
            maxHeight: '200px',
            overflowY: 'auto'
          }}>
            {testResults.map((result, index) => (
              <div key={index}>{result}</div>
            ))}
          </div>
        </div>
      )}

      <div>
        <h3> </h3>
        <ul style={{ textAlign: 'left', lineHeight: '1.6', fontSize: '14px' }}>
          <li><strong>Performance :</strong> </li>
          <li><strong>Console :</strong>  SDK </li>
          <li><strong>Network :</strong> </li>
          <li><strong>:</strong>  Memory </li>
          <li><strong>FPS :</strong> </li>
          <li><strong>:</strong> </li>
        </ul>
      </div>
    </div>
  );
}

export default PerformanceTest;