import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';

function BehaviorTest() {
  const navigate = useNavigate();
  const [clickCount, setClickCount] = useState(0);
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    message: ''
  });
  const [scrollPosition, setScrollPosition] = useState(0);

  // 
  useEffect(() => {
    const handleScroll = () => {
      setScrollPosition(window.pageYOffset);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  //  - 
  const simulateShoppingCart = () => {
    (window as any).monitor?.trackBusiness('add_to_cart', {
      productId: 'PROD-001',
      productName: '',
      quantity: 1,
      price: 99.99
    });

    (window as any).monitor?.trackEvent('user', '', {
      product: 'PROD-001',
      timestamp: Date.now()
    });
  };

  // 
  const simulateLogin = () => {
    (window as any).monitor?.trackBusiness('login_attempt', {
      method: 'email',
      timestamp: Date.now()
    });

    // 
    setTimeout(() => {
      (window as any).monitor?.trackBusiness('login_success', {
        userId: 'USER-123',
        loginTime: Date.now()
      });
    }, 1000);
  };

  // 
  const simulatePayment = () => {
    const steps = ['initiate_payment', 'select_method', 'confirm_payment', 'payment_success'];

    steps.forEach((step, index) => {
      setTimeout(() => {
        (window as any).monitor?.trackBusiness(step, {
          orderId: 'ORDER-456',
          amount: 199.98,
          step: index + 1,
          totalSteps: steps.length
        });
      }, index * 500);
    });
  };

  // 
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    (window as any).monitor?.trackEvent('user', '', {
      formType: 'contact',
      fields: Object.keys(formData),
      timestamp: Date.now()
    });

    alert('');
  };

  // 
  const createClickableElements = () => {
    const elements = [];
    for (let i = 0; i < 20; i++) {
      elements.push(
        <button
          key={i}
          className="test-button"
          style={{ margin: '2px' }}
          onClick={() => {
            setClickCount(prev => prev + 1);
            (window as any).monitor?.trackEvent('user', ` #${i + 1}`, {
              buttonId: i + 1,
              totalClicks: clickCount + 1
            });
          }}
        >
           {i + 1}
        </button>
      );
    }
    return elements;
  };

  return (
    <div className="test-section">
      <h2> </h2>

      <div style={{ marginBottom: '20px' }}>
        <Link to="/">
          <button className="test-button"> </button>
        </Link>
        <button
          className="test-button"
          onClick={() => navigate('/about')}
        >
           ()
        </button>
      </div>

      <div className="test-status info">
         :  {clickCount} |  {scrollPosition.toFixed(0)}px
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <button className="test-button" onClick={simulateShoppingCart}>
          
        </button>
        <button className="test-button" onClick={simulateLogin}>
          
        </button>
        <button className="test-button" onClick={simulatePayment}>
          
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <p></p>
        <div style={{ display: 'flex', flexWrap: 'wrap', gap: '4px' }}>
          {createClickableElements()}
        </div>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <form onSubmit={handleSubmit} style={{ maxWidth: '400px' }}>
          <div style={{ marginBottom: '12px' }}>
            <label style={{ display: 'block', marginBottom: '4px' }}>:</label>
            <input
              type="text"
              value={formData.username}
              onChange={(e) => setFormData(prev => ({ ...prev, username: e.target.value }))}
              style={{ width: '100%', padding: '8px', borderRadius: '4px', border: '1px solid #ddd' }}
              placeholder=""
            />
          </div>

          <div style={{ marginBottom: '12px' }}>
            <label style={{ display: 'block', marginBottom: '4px' }}>:</label>
            <input
              type="email"
              value={formData.email}
              onChange={(e) => setFormData(prev => ({ ...prev, email: e.target.value }))}
              style={{ width: '100%', padding: '8px', borderRadius: '4px', border: '1px solid #ddd' }}
              placeholder=""
            />
          </div>

          <div style={{ marginBottom: '12px' }}>
            <label style={{ display: 'block', marginBottom: '4px' }}>:</label>
            <textarea
              value={formData.message}
              onChange={(e) => setFormData(prev => ({ ...prev, message: e.target.value }))}
              style={{ width: '100%', padding: '8px', borderRadius: '4px', border: '1px solid #ddd', height: '80px' }}
              placeholder=""
            />
          </div>

          <button type="submit" className="test-button">
            
          </button>
        </form>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <input
          type="text"
          placeholder=""
          style={{ width: '100%', padding: '12px', borderRadius: '4px', border: '1px solid #ddd' }}
          onKeyDown={(e) => {
            if (e.key === 'Enter') {
              (window as any).monitor?.trackEvent('user', ' Enter ', {
                inputValue: e.currentTarget.value,
                timestamp: Date.now()
              });
            }
          }}
        />
        <p style={{ fontSize: '12px', color: '#666' }}>
            Enter 
        </p>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <p>: {scrollPosition.toFixed(0)}px</p>
        <div style={{ height: '300px', background: '#f9f9f9', overflowY: 'scroll', padding: '16px' }}>
          <div style={{ height: '800px' }}>
            <h4></h4>
            <p></p>
            {[...Array(20)].map((_, i) => (
              <p key={i} style={{ margin: '16px 0' }}>
                  {i + 1} - 
              </p>
            ))}
          </div>
        </div>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <button
          className="test-button"
          onClick={() => {
            (window as any).monitor?.trackEvent('user', '', {
              step: 'browse_products',
              category: 'electronics',
              timestamp: Date.now()
            });
          }}
        >
          
        </button>

        <button
          className="test-button"
          onClick={() => {
            (window as any).monitor?.trackEvent('user', '', {
              productId: 'PROD-999',
              productName: '',
              price: 2999,
              timestamp: Date.now()
            });
          }}
        >
          
        </button>

        <button
          className="test-button"
          onClick={() => {
            (window as any).monitor?.trackEvent('user', '', {
              type: 'suggestion',
              rating: 5,
              feedback: '',
              timestamp: Date.now()
            });
          }}
        >
          
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <p></p>
        <button
          className="test-button"
          onClick={() => {
            // 
            (window as any).monitor?.trackEvent('navigation', '', {
              page: 'behavior-test',
              action: 'manual_visit_record',
              timestamp: Date.now()
            });
          }}
        >
          
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <button
          className="test-button"
          onClick={() => {
            // 
            const steps = [
              { action: '', delay: 0 },
              { action: '', delay: 500 },
              { action: '', delay: 1000 },
              { action: '', delay: 1500 },
              { action: '', delay: 2000 },
            ];

            steps.forEach((step, index) => {
              setTimeout(() => {
                (window as any).monitor?.trackEvent('user', step.action, {
                  flowStep: index + 1,
                  totalSteps: steps.length,
                  flowId: 'shopping_flow_001',
                  timestamp: Date.now()
                });
              }, step.delay);
            });
          }}
        >
          
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> UI </h3>
        <div style={{ display: 'flex', gap: '8px', flexWrap: 'wrap' }}>
          <button
            className="test-button"
            onMouseEnter={() => {
              (window as any).monitor?.trackEvent('user', '', {
                element: 'hover-button',
                timestamp: Date.now()
              });
            }}
          >
            
          </button>

          <button
            className="test-button"
            onFocus={() => {
              (window as any).monitor?.trackEvent('user', '', {
                element: 'focus-button',
                timestamp: Date.now()
              });
            }}
          >
            
          </button>

          <button
            className="test-button"
            onContextMenu={(e) => {
              e.preventDefault();
              (window as any).monitor?.trackEvent('user', '', {
                element: 'context-menu-button',
                timestamp: Date.now()
              });
            }}
          >
            
          </button>
        </div>
      </div>

      <div>
        <h3> </h3>
        <ul style={{ textAlign: 'left', lineHeight: '1.6', fontSize: '14px' }}>
          <li><strong>:</strong> </li>
          <li><strong>:</strong> usernavigation</li>
          <li><strong>:</strong> </li>
          <li><strong>:</strong> </li>
          <li><strong>:</strong> </li>
          <li><strong>:</strong> </li>
          <li><strong>:</strong> </li>
        </ul>
      </div>

      {/*  */}
      <div style={{ height: '400px', background: 'linear-gradient(to bottom, #f0f0f0, #e0e0e0)', padding: '20px', marginTop: '40px' }}>
        <h4> </h4>
        <p></p>
        {[...Array(15)].map((_, i) => (
          <div key={i} style={{ margin: '20px 0', padding: '16px', background: 'white', borderRadius: '4px' }}>
            <h5> {i + 1}</h5>
            <p>SDK </p>
            {i % 5 === 4 && (
              <button
                className="test-button"
                onClick={() => {
                  (window as any).monitor?.trackEvent('user', ` - ${i + 1}`, {
                    scrollPosition: window.pageYOffset,
                    blockIndex: i + 1,
                    timestamp: Date.now()
                  });
                }}
              >
                
              </button>
            )}
          </div>
        ))}
      </div>
    </div>
  );
}

export default BehaviorTest;