// src/components/TestComponents.tsx - 
import React, { useState } from 'react';

const TestComponents: React.FC = () => {
  const [count, setCount] = useState(0);
  const [asyncLoading, setAsyncLoading] = useState(false);

  // JavaScript 
  const triggerJSError = () => {
    console.log(' JavaScript ');
    //  null 
    const obj: any = null;
    if (obj === null) {
      throw new Error(' JavaScript  null ');
    }
    obj.nonExistentProperty.someMethod();
  };

  // Promise 
  const triggerPromiseError = async () => {
    setAsyncLoading(true);
    console.log(' Promise ');

    try {
      await new Promise((resolve, reject) => {
        setTimeout(() => {
          reject(new Error(''));
        }, 1000);
      });
    } catch (error) {
      setAsyncLoading(false);
      throw error; //  SDK 
    }
  };

  // HTTP 
  const triggerHttpError = async () => {
    console.log(' HTTP ');

    try {
      const response = await fetch('http://192.168.1.161:8080/api/nonexistent-endpoint', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'X-Test-Header': 'error-test'
        },
        body: JSON.stringify({
          testData: 'error simulation',
          timestamp: Date.now()
        })
      });

      if (!response.ok) {
        console.error(`HTTP : ${response.status} ${response.statusText}`);
      }
    } catch (error) {
      console.error(':', error);
    }
  };

  // 
  const triggerResourceError = () => {
    console.log('');

    const img = document.createElement('img');
    img.id = 'test-broken-image';
    img.src = 'http://localhost:3000/nonexistent-image.jpg';
    img.alt = '';
    img.style.display = 'none';
    document.body.appendChild(img);

    setTimeout(() => {
      if (document.body.contains(img)) {
        document.body.removeChild(img);
      }
    }, 2000);
  };

  // 
  const simulateBusinessAction = (actionType: string) => {
    console.log(`: ${actionType}`);

    // 
    if (actionType === 'purchase') {
      setCount(prev => prev + 1);
    } else if (actionType === 'save') {
      localStorage.setItem('testData', JSON.stringify({
        count,
        timestamp: Date.now()
      }));
    }
  };

  // 
  const triggerLongTask = () => {
    console.log('');

    const startTime = Date.now();
    while (Date.now() - startTime < 100) {
      // 
    }
    console.log('');
  };

  // ...
  return (
    <div className="test-components" id="test-container">
      <h2 id="test-page-title"></h2>

      {/*  */}
      <section className="test-section" id="error-test-section">
        <h3 id="error-test-title"></h3>
        <div className="test-buttons" id="error-test-buttons">
          <button
            id="trigger-js-error"
            className="test-button danger"
            onClick={triggerJSError}
            data-test-type="js-error"
          >
             JS 
          </button>

          <button
            id="trigger-promise-error"
            className="test-button danger"
            onClick={triggerPromiseError}
            disabled={asyncLoading}
            data-test-type="promise-error"
          >
            {asyncLoading ? '...' : ' Promise '}
          </button>

          <button
            id="trigger-http-error"
            className="test-button danger"
            onClick={triggerHttpError}
            data-test-type="http-error"
          >
             HTTP 
          </button>

          <button
            id="trigger-resource-error"
            className="test-button danger"
            onClick={triggerResourceError}
            data-test-type="resource-error"
          >
            
          </button>
        </div>
      </section>

      {/*  */}
      <section className="test-section" id="performance-test-section">
        <h3 id="performance-test-title"></h3>
        <div className="test-buttons" id="performance-test-buttons">
          <button
            id="trigger-long-task"
            className="test-button warning"
            onClick={triggerLongTask}
            data-test-type="performance"
          >
            
          </button>

          <button
            id="reload-page"
            className="test-button"
            onClick={() => window.location.reload()}
            data-test-type="navigation"
          >
            
          </button>
        </div>
      </section>

      {/*  */}
      <section className="test-section" id="behavior-test-section">
        <h3 id="behavior-test-title"></h3>
        <div className="behavior-controls" id="behavior-controls">
          <div className="counter-section" id="counter-section">
            <span id="counter-label">: </span>
            <span id="counter-value">{count}</span>
            <button
              id="increment-counter"
              className="test-button"
              onClick={() => {
                setCount(prev => prev + 1);
                simulateBusinessAction('increment');
              }}
              data-test-type="user-action"
            >
              
            </button>
            <button
              id="decrement-counter"
              className="test-button"
              onClick={() => {
                setCount(prev => Math.max(0, prev - 1));
                simulateBusinessAction('decrement');
              }}
              data-test-type="user-action"
            >
              
            </button>
          </div>

          <div className="business-actions" id="business-actions">
            <button
              id="simulate-purchase"
              className="test-button success"
              onClick={() => simulateBusinessAction('purchase')}
              data-test-type="business-action"
              data-business-type="purchase"
            >
              
            </button>

            <button
              id="simulate-save"
              className="test-button"
              onClick={() => simulateBusinessAction('save')}
              data-test-type="business-action"
              data-business-type="save"
            >
              
            </button>

            <button
              id="simulate-share"
              className="test-button"
              onClick={() => {
                if (navigator.share) {
                  navigator.share({
                    title: 'Mini Sentry ',
                    text: '',
                    url: window.location.href
                  }).catch(err => console.log(':', err));
                } else {
                  console.log(' Web Share API');
                }
              }}
              data-test-type="business-action"
              data-business-type="share"
            >
              
            </button>
          </div>
        </div>
      </section>

      {/* ... */}
    </div>
  );
};

export default TestComponents;